class Auth {
    constructor() {
        this.authenticated = localStorage.getItem("Login");
    }

    login(cb) {
        localStorage.setItem("Login", true);
        this.authenticated = localStorage.getItem("Login");
        cb();
    }

    logout(cb) {
        localStorage.setItem("Login",false);
        this.authenticated = localStorage.getItem("Login");
        cb();
    }

    isAuthenticated() {
        return this.authenticated;
    }
}

export default new Auth();